import myList from "../../common/dictionaries/aboutMeDic.js"
export default {
	data() {
		return {
			headImg:require("@/static/images/bgi_hw.png"),
			topBgI:require("@/static/images/mybgi.png"),
			myList:myList
		}
	},
	onLoad:function(){
		uni.onNetworkStatusChange(function (res) {
			if(!res.isConnected){
				uni.showToast({
				    title: "网络断开",
				    duration: 2000,
					icon:"none"
				});
			}
		});
	},
	methods: {
		// 扫码功能
		saoyisao(){
			uni.scanCode({
			    success: function (res) {
					uni.showToast({
					    title: res.result,
					    duration: 2000,
						icon:"none"
					});
			        // console.log('条码类型：' + res.scanType);
			        // console.log('条码内容：' + res.result);
			    }
			})
		},
		//图片上传功能
		updateHead(){
			let that = this;
			uni.chooseImage({
			    count: 1, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album','camera'], //从相册选择
			    success: function (res) {
			        that.headImg = res.tempFilePaths[0]
			    }
			})
		},
		//其他功能校验
		checkFunc(index){
			switch(index){
				case 0:
					//查看网络
					uni.getNetworkType({
					    success: function (res) {
							uni.showToast({
							    title: '当前网络连接是:'+res.networkType,
							    duration: 2000,
								icon:"none"
							});
					    }
					});
					break;
				case 1:
					uni.getLocation({
					    type: 'wgs84',
					    success: function (res) {
							uni.showToast({
							    title:'经度:' + res.longitude+'纬度:' + res.latitude,
							    duration: 2000,
								icon:"none"
							})
					    }
					});
					 break;
				case 2:
					uni.navigateTo({
					    url: '../otherPage/otherPage?test=previousPageInfo'
					});
					 break;
				case 3:
					
					 break;
				default:
			}
		}
	}
}
